import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Navigation from './pages/navigation';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import Home from './pages/home';
import About from './pages/about';
import Chatbot from './pages/chatbot'; // Assuming this is the correct path to your Chatbot component
import './App.css';

function App() {
  const [showChatbot, setShowChatbot] = useState(false);
  const [isDarkMode, setDarkMode] = useState(false); // State for dark mode

  // Function to toggle chatbot
  const openChatbot = () => {
    setShowChatbot(true);
  };

  // Function to toggle dark mode
  const toggleDarkMode = () => {
    setDarkMode(prevMode => !prevMode);
  };
  
  return (
    <div className={isDarkMode ? 'dark' : 'light'}> {/* Apply dark mode class to the body */}
      <Router>
        <Navigation openChatbot={openChatbot} isDarkMode={isDarkMode} />
        <TransitionGroup>
          <CSSTransition classNames="fade-in" timeout={500}>
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/about" element={<About />} />
              <Route path="/chatbot" element={<Chatbot showChatbot={showChatbot} setShowChatbot={setShowChatbot} isDarkMode={isDarkMode} />} />
            </Routes>
          </CSSTransition>
        </TransitionGroup>
        </Router>
    </div>
  );
}

export default App;
